
































import { Component, Prop, Vue } from "vue-property-decorator";
import axios from 'axios';

@Component
export default class Step1 extends Vue {
    // private file!: File;
    triggerInputFile() {
        const inputHTMLInputElement = document.getElementById("csvfile");
        inputHTMLInputElement?.click();
    }

    async uploadCSV(evt: any) {
        evt.preventDefault();
        // const vm = this;
        // e.submit();
        /** disable eslint */
        const file = (this.$refs.file as any).files[0];
        // console.log(file);
        const formData = new FormData();
        formData.append('csvfile', file);
        try {
            const resp = await axios.post( 'http://localhost:8000/api/v1/upload_csv',
                formData,
                {
                    headers: {
                        'Content-Type': 'multipart/form-data'
                    }
                })
                .catch(function(err) {
                    throw new Error(err);
                });
            // console.log('SUCCESS!!', (resp as any).data.data.file);
            localStorage.setItem('file', (resp as any).data.data.file);
            this.$emit('input', {action: 'next'});
        } catch(ex) {
            this.$emit('input', {action: 'error', error: ex.message});
        }
    }
}

