








































import { Component, Prop, Vue } from 'vue-property-decorator';
import axios from 'axios';

@Component
export default class Step3 extends Vue {
    @Prop() Mapper!:object;
    @Prop() Data!:object;
    async mounted() {
        this.Mapper = JSON.parse(localStorage.getItem('mapping') || "{}");
        const formData = new FormData();
        formData.append('remap', localStorage.getItem('mapping') || "");

        const fileName = localStorage.getItem('file') || "";
        const resp = await axios.post( `http://localhost:8000/api/v1/file/${fileName}/sample`,
            formData
            )
            .catch(function() {
                console.log('FAILURE!!');
            });
        // console.log('SUCCESS!!', (resp as any).data.data);
        // console.log(resp);
        this.Data = (resp as any).data.data;
    }

    sendConfirmation(evt: any) {
        evt.preventDefault();
        // e.submit();
        this.$emit('input', {action: 'next'});
    }

    onBack() {
        this.$emit('input', {action: 'back'})
    }
}
