







































import { Component, Prop, Vue } from 'vue-property-decorator';
import axios from 'axios';

@Component
export default class Step4 extends Vue {
    radioGroup = 1;
   @Prop() private inputs = [
       {text: 'Update them with the data in the spreadsheet, but only for fields that are empty (recommended)', value: 1},
       {text: 'Update them with the data in the spreadsheet, including fields that already have a value', value: 2},
       {text: 'Don\'t modify them', value: 3}
   ];

    async submitForm(evt: any) {
        evt.preventDefault();
        // e.submit();
        try {
            const formData = new FormData();
            formData.append('remap', localStorage.getItem('mapping') || "");
            formData.append('duplicate_strategy', this.radioGroup.toString());

            const fileName = localStorage.getItem('file') || "";
            const resp = await axios.post( `http://localhost:8000/api/v1/file/${fileName}/save`,
                formData
                )
                .catch(function(err) {
                    throw new Error(err);
                });
            console.log(this.radioGroup);
            this.$emit('input', {action: 'success'});
        } catch (ex) {
            this.$emit('input', {action: 'error', error: ex.message})
        }
    }

    onBack() {
        this.$emit('input', {action: 'back'})
    }
}

