























import { Component, Prop, Vue } from 'vue-property-decorator';
import Step1 from '@/components/Step1.vue';
import Step2 from '@/components/Step2.vue';
import Step3 from '@/components/Step3.vue';
import Step4 from '@/components/Step4.vue';

@Component({
    components: {
        Step1,
        Step2,
        Step3,
        Step4
    }
})
export default class Import extends Vue {
    private _e1 = 0;
    @Prop() success!:boolean;
    @Prop() error!:string;
    @Prop() e1!:number;
    private steps: Array<object> = [
        {name: 'Import Contacts', description: '<b>Step 1:</b> Upload CSV<br><div class="text-subtitle-2 text-left ma-2">File must be CSV</div>'},
        {name: '', description: '<b>Step 1:</b> Upload CSV<br><div class="text-subtitle-2 text-center ma-2">File must be CSV</div>'},
        {name: '', description: '<b>Step 1:</b> Upload CSV<br><div class="text-subtitle-2 text-center ma-2">File must be CSV</div>'},
        {name: '', description: '<b>Step 1:</b> Upload CSV<br><div class="text-subtitle-2 text-center ma-2">File must be CSV</div>'}
    ];
    created() {
        this.e1 = 1;
        this.error = '';
        // console.log(this.e1);
    }
    mounted() {
        // console.log('Mounted: ', this.e1);
        this.e1 = 1;
    }

    private getStep(index: number): number {
        return index + 1;
    }

    private nextStep() {
        this.e1 += 1;
        this.error = '';
        console.log(this.e1);
    }

    private backStep() {
        this.e1 -= 1;
        this.error = '';
        console.log(this.e1);
    }

    onAction({action, error}: any) {
        console.log({action});
        switch (action) {
            case 'next':
                this.nextStep();
                break;

            case 'back':
                this.backStep();
                break;

            case 'success':
                this.success = true;
                setTimeout(() => {
                    this.$router.push("/");
                }, 2000);
                break;

            case 'error':
                this.success = false;
                this.error = error;
                break;
        }
    }
}

